.tf-dynamic-text-field {
  display: flex;
  align-items: center;
  position: relative;
  gap: $global-spacer-sm;

  .tf-dynamic-text-popover {
    position: absolute;
    top: 100%;
    z-index: $popover-index;
  }

}

.tf-dynamic-text-item {
  border: 1px solid transparent; 
	background-color: $global-theme-color-lighter-05;
	color: $field-focus-color;
	border-radius: $field-border-radius;
	font-size: 13px;
	font-weight: bold;
	line-height: 1.4;
  padding: 0.25em 0.5em;
  display: inline-flex;
  gap: 0.35em;
  align-items: center;
  margin: 0 0.15em; // Make it easier to see cursor before and after item
  
  &::before {
    content: url("data:image/svg+xml;base64,");
    line-height: 1;
  }
}

.tf-dynamic-text-input {
  @include field;
	padding: 0;
	
	&:focus-within {
		@include field-focus;
	}

  & > .cm-editor {
    width: 100%;
    height: 100%;
    padding: $field-padding;
    padding-top: 0;
    padding-bottom: 0;
		
		&.cm-focused {
			outline: none;
		}
		
		.cm-scroller {
			font-family: inherit;
		}

    .cm-line,
    .cm-content {
      padding: 0;
    }

    .cm-content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
    }
  }
}
